"use client";

import { useState } from "react";
import { BudgetProvider } from "./context/BudgetContext";
import BudgetForm from "./components/BudgetForm/BudgetForm";
import BudgetList from "./components/BudgetList/BudgetList";
import ChartPage from "./components/Chart/Chart";
import CategoryForm from "./components/Category/Category";
import { CategoryProvider } from "./context/CategoryContext";
import styles from "./page.module.css";

import Box from "@mui/material/Box";
import RadioGroup from "@mui/material/RadioGroup";
import Radio from "@mui/material/Radio";
import FormControl from "@mui/material/FormControl";
import FormControlLabel from "@mui/material/FormControlLabel";
import FormLabel from "@mui/material/FormLabel";
import { ThemeProvider, createTheme } from "@mui/material/styles";

// Tema geçiş fonksiyonu
function ThemeToggle({
  setMode,
}: {
  setMode: React.Dispatch<React.SetStateAction<"light" | "dark" | "system">>;
}) {
  return (
    <Box
      sx={{
        display: "flex",
        width: "100%",
        alignItems: "center",
        justifyContent: "center",
        bgcolor: "background.default",
        color: "text.primary",
        borderRadius: 1,
        p: 3,
        minHeight: "56px",
      }}
    >
      <FormControl>
        <FormLabel id="demo-theme-toggle">Tema</FormLabel>
        <RadioGroup
          aria-labelledby="demo-theme-toggle"
          name="theme-toggle"
          row
          onChange={(event) =>
            setMode(event.target.value as "system" | "light" | "dark")
          }
        >
          <FormControlLabel value="system" control={<Radio />} label="Sistem" />
          <FormControlLabel
            value="light"
            control={<Radio />}
            label="Aydınlık"
          />
          <FormControlLabel value="dark" control={<Radio />} label="Karanlık" />
        </RadioGroup>
      </FormControl>
    </Box>
  );
}

export default function Home() {
  const [activeComponent, setActiveComponent] = useState<
    "form" | "list" | "chart" | "category" | null
  >("form");
  const [mode, setMode] = useState<"light" | "dark" | "system">("system");

  // Tema ayarları
  const currentTheme = createTheme({
    palette: {
      mode: mode === "system" ? "light" : mode, // Sistem tercihi ile mod ayarlanır.
    },
  });

  return (
    // ThemeProvider tüm sayfayı kapsayacak şekilde yerleştirildi
    <ThemeProvider theme={currentTheme}>
      <BudgetProvider>
         
      </BudgetProvider>
    </ThemeProvider>
  );
}
